import { Style } from "./style";

const old_message:Message[]=[]
const new_message: Message[] = []

export class Message{
    el=document.createElement('msg-llej')
    private autoHideTime=1000*3
    constructor(par: Message_Data){
        this.setThis(par);
        new_message.push(this)
    }
    /** è¿›è¡Œä¸€äº›èµ‹å€¼å·¥ä½œ */
    private setThis({ style=Style.message,msg}:Message_Data) {
        this.el.innerHTML = `
        <div style="${style}">${msg}</div>
        `;
    }

    /** å±•ç¤ºel */
    show(){
        document.body.appendChild(this.el)
        return this
    }
    /** éšè—el */
    hide() {
        this.el.remove()
        old_message.push(this)
        return this
    }
    /** å±•ç¤ºel  autoHideTime æ¯«ç§’åéšè—*/
    autoHide(){
        this.show()
        setTimeout(() => {
            this.hide()
        }, this.autoHideTime);
        return this
    }
    /** è·å–ä¸€ä¸ªMessagå¯¹è±¡ï¼Œå®ƒä¸ä¸€å®šæ˜¯æ–°çš„ã€‚è¿™æ˜¯ä¸ºäº†ä¼˜åŒ–å†…å­˜å ç”¨ */
    static getMessage(par:Message_Data){
        if(old_message.length===0){/** æ²¡æœ‰æ—§çš„å¯¹è±¡ */
            return new Message(par)
        }
        const msg=old_message.pop()
        msg.setThis(par)
        new_message.push(msg)
        return msg
    }
}


export interface Message_Data{
    msg: string
    style?:string
}